.toggle{
    display: flex;
    justify-content: space-between;
    border: 3px solid var(--orange);
    border-radius: 1rem;
    padding: 2px;
    position: relative;
    cursor: pointer;
}

.toggle>*{
 width: 1rem;
 height: 1rem;
 color: var(--orange);
}

.t-button{
border-radius: 100%;
background: var(--orange);
position: absolute;
}